html, body {
    min-height: 100%;
    height: 100%;
    font: 14px "Century Gothic", Futura, sans-serif;
    background-color: black;
    color: white;
  }
.BuildInfo {
    text-align: center;
}
.title {
    font: 50px "Century Gothic", Futura, sans-serif;
    margin: none;
    padding: none;
}
.PrimarySection {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}
.BoxInfo {
    border-style: solid;
    border-width: 3px;
    border-color: white;
    border-radius: 8px;
    padding: 5px;
    margin: 15px;
}
.GeneratedUrl{
    word-wrap: break-word;
    display: inline;
    font: 10px "Century Gothic", Futura, sans-serif;
}
.SectionHeader {
    font: 60px "Century Gothic", Futura, sans-serif;
}
.subtitle {
    font: 30px "Century Gothic", Futura, sans-serif;
}
.subtitle2 {
    font: 20px "Century Gothic", Futura, sans-serif;
}
.UrlGenerator{
    max-width: 700px;
    min-width: 500px;
    
    text-align: left;
}
.InfoCollumn {
    width: 300px;
    float:left;
    padding: 10px;
}
.Loadout-body {
    padding: 20px;
    height:100%;
    max-width:960px;
    margin-left: auto;
    margin-right: auto;
    background-color: rgba(48, 48, 48, 0.75); 
    text-align: center;
}